<template>
  <div class="home">
    <div class="banner">
      <div class="title">
      </div>
      <div class="content">
        <ul>
          <li v-for="(item,index) in learnList"
              :key="index"
              @click="click(item.url)">{{item.name}}</li>
        </ul>
      </div>
    </div>

    <div class="github-right"><a href="https://github.com/FFSZ-5/myblog"
         target="_blank"><i class="github"></i><span>GitHub</span></a></div>
  </div>
</template>

<script>
import { getFileName, storeFile } from '@/units/tool.js'
export default {
  data () {
    return {
      vanta: null,
      learnList: {}
    }
  },
  mounted () {
    this.getList()
  },
  beforeDestroy () {
    if (this.vanta) {
      this.vanta.destroy()
    }
  },
  methods: {
    click (val) {
      this.$router.push({ path: val })
    },
    getList () {
      var arr = getFileName('mdfile')
      arr.map((element) => {
        if (!Object.keys(this.learnList).includes(element.split('/')[0])) {
          this.learnList[element.split('/')[0]] = element.split('/')[0] + '/' + element.split('/')[1].split('-')[1].split('.')[0]
        }
      })
      // this.learnList = JSON.parse(JSON.stringify(this.learnList))
      this.learnList = storeFile(this.learnList, true)
      console.log(this.learnList)
    }
  }
}
</script>
